@font-face {
  font-family: "adamwarrenpro";
  src: url("./adamwarrenpro.ttf")
}

* {
  box-sizing: border-box;
  margin: 0;
}

@font-face {
  font-family: "adamwarrenpro-bold";
  src: url("./adamwarrenpro-bold.ttf")
}

@font-face {
  font-family: "badaboom";
  src: url("./BadaboomBB_Reg.otf")
}

.container {
  width: 100%;
  font-family: "adamwarrenpro"
}

header {
  display: grid;
  grid-template-columns: 3fr 1fr;
  font-family: "badaboom";
  max-height: 150px;
  background-color: #af0521;
  padding: 20px;
  color: #eaea26;
  font-size: 18px;
  text-shadow: -1px 1px 0 #000,
    1px 1px 0 #000,
    1px -1px 0 #000;
  text-align: center;
}

.content {
  width: 95%;
  margin: 20px auto;
  background-color: white;
  padding: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
}

.card {
  margin-top: 30px;
  cursor: pointer;
  text-align: center;
  background-color: #be2f2f;
  color: #eaea26;
  border-radius: 10px;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 8.3px 7.9px rgba(0, 0, 0, 0.072),
    0 15.8px 18.4px rgba(0, 0, 0, 0.086),
    0 20px 30px rgba(0, 0, 0, 0.12);
}

.card-content {
  margin: 10px;
}

.card-image {
  width: 100px;
  height: 175px;
  border-radius: 5px;
  margin-bottom: 10px;
}

@media (min-width: 860px) {
  header {
    display: grid;
    grid-template-columns: 3fr 1fr;
    font-family: "badaboom";
    height: 100px;
    background-color: #af0521;
    padding: 20px;
    color: #eaea26;
    font-size: 27px;
    text-shadow: -1px 1px 0 #000,
      1px 1px 0 #000,
      1px -1px 0 #000;
    text-align: center;
  }

  .content {
    width: 90%;
    margin: 20px auto;
    background-color: #d5cdcd;
    padding: 10px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
  }


  .card-image {
    width: 200px;
    height: 350px;
  }
}

@media (min-width: 1100px) {
  .content {
    width: 75%;
    padding: 20px;
  }
}

@media (min-width: 1320px) {
  .content {
    padding: 30px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}